<template>
  <div>
    <b-card no-body style="width: 100%; height: 100%">
      <b-tabs pills card vertical nav-wrapper-class="col-2">
        <b-tab disabled>
          <template #title>
            <br>
            <h4 align="center">
              难度选择
            </h4>
            <hr>
          </template>
        </b-tab>
        <b-tab active class="bg-rect-background" @click="changeLevel(1)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 1
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(2)">
          <div @click="changeLevel(2)"></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 2
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(3)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 3
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(4)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 4
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(5)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 5
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(6)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 6
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(7)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 7
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(8)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 8
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
        <b-tab class="bg-rect-background" @click="changeLevel(9)">
          <div></div>
          <br>
          <template #title>
            <div class="level-title" align="center">
              level 9
            </div>
          </template>
          <practice-right :problem_lst="problem_lst" :level="level"></practice-right>
        </b-tab>
      </b-tabs>
    </b-card>
  </div>
</template>

<script>
import PracticeRight from "@/components/practice/practice-right";

export default {
  name: "choose-level",
  components: {PracticeRight},
  data() {
    return {
      level: 1,
      problem_lst: [],
    }
  },
  mounted() {
    this.loadQuestion()
  },
  methods: {
    changeLevel(lev) {
      this.level=lev
      localStorage.setItem('problemlevel', lev)
      // console.log(localStorage.getItem('problemlevel'))
      // console.log(this.level)
      this.loadQuestion()
    },
    async loadQuestion(){
      this.$axios.
      post("/problem/page", {
        accountId: "bsq",
        problemLevel: this.level,
        problemCount: 20
      })
          .then((res) => {
            // alert("发送成功");
            // console.log(res.data);

            if (res.data.code === 0) {
              this.problem_lst=res.data.data.problemInfoList
              // console.log(this.problem_lst[2].problemId)
            }
            // alert(res.data.msg);
          })
    },
  }
}
</script>

<style scoped>

.level-title{
  font-size: large;
  vertical-align: middle
}

.bg-rect-background{
  height: 100%;
}

</style>